.mainContainer {
	display: flex;
	/* justify-content: space-between; */
	flex-direction: column;
}

.searchBar {
	display: flex;
	flex-grow: 1;
}

.searchBar form {
	width: 100%;
	display: flex;
    height: 100%;
    border-radius: 0;
}

.select {
    margin-top: 1rem;
}

@media only screen and (min-width: 600px) {
    .mainContainer {
        flex-direction: row;
    }

	.searchBar {
		padding-right: 1rem;
	}

    .select {
        margin: 0;
    }
}
